@import "fonts";
@import "null";
@import "mixins";
@import "./vendors/swiper";

$orange: #FC880D;
$green: #227650;
$gold: #9E7E4B;
$darkBlue: #282739;
$red: #E31235;
$laptop: 1360px;
$tablet: 1080px;
$tabletS: 991.92px;
$mobile: 767.92px;
$mobileMedium: 575.98px;
$mobileMini: 400px;
@import "forms";
@import "typegr";
@import "btns";
@import "modal";
@import "./layout/header";
@import "./layout/footer";
@import "./components/nav";


html {
  scroll-behavior: smooth;
}

body.blocked,
body.lock {
  overflow: hidden;
}

body.overlayed {
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
    cursor: pointer;
    backdrop-filter: blur(5.5px);
    background: #36363666;
  }
}

.container {
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  max-width: 1680px;
  @media (max-width: 1760px) {
    max-width: 100%;
    padding: 0 40px;
  }
  @media (max-width: 1080px) {
    max-width: 1000px;
    padding: 0;
  }
  @media(max-width: 1040px) {
    max-width: 100%;
    padding: 0 40px;
  }
  @media (max-width: $mobileMedium) {
    padding: 0 16px;
  }
}

.container-narrow {
  max-width: 980px;
  margin: 0 auto;
}

body {
  font-family: "Jost", sans-serif;
  color: $darkBlue;
  font-size: 20px;
  line-height: 28px;
  font-weight: 400;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  @media (max-width: $tablet) {
    font-size: 22px;
    line-height: 30px;
  }
  @media (max-width: $tablet) {
    font-size: 16px;
    line-height: 22px;
  }
  @media (max-width: $mobile) {
    font-size: 16px;
    line-height: 24px;
  }
}

main {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
}

.l-label {
  position: absolute;
  top: 12px;
  left: 12px;
  display: inline-block;
  font-size: 16px;
  font-weight: 400;
  line-height: 23.12px;
  border-radius: 8px;
  color: #fff;
  padding: 8px;
  backdrop-filter: blur(19.600000381469727px);
  z-index: 1;

  &::before {
    content: "";
    position: absolute;
    top: -1px;
    left: 0px;
    right: 0px;
    bottom: -1px;
    border-radius: 8px;
    padding: 1px; /*   */
    background: linear-gradient(to bottom, #fff0 0%, #ffffff80 100%);
    -webkit-mask: linear-gradient(#fff 0 0) content-box,
    linear-gradient(#fff 0 0);
    -webkit-mask-composite: xor;
    mask-composite: exclude;
    z-index: -1;
  }
}

.about-welcome {
  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    min-height: 556px;
    border-radius: 16px;
    overflow: hidden;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    @media (max-width: $mobileMedium) {
      min-height: 126vw;
    }

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      right: 30%;
      background: linear-gradient(73.21deg, #6F73EE 7.37%, rgba(111, 115, 238, 0) 80.02%);
      @media (max-width: $tabletS) {
        bottom: -30%;
        right: -10%;
        background: linear-gradient(27.49deg, #6F73EE 29.64%, rgba(111, 115, 238, 0) 85.31%);
      }
    }
  }

  &__info {
    position: relative;
    max-width: 680px;
    z-index: 1;
    color: #fff;
    padding: 40px;
    @media (max-width: $tabletS) {
      padding: 32px;
      max-width: 510px;
    }
    @media (max-width: $mobileMedium) {
      max-width: 100%;
      padding: 16px;
    }

  }

  &__title {
    margin-bottom: 24px;
  }

  &__num-features {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @media (max-width: $mobileMedium) {
      margin-bottom: -12px;
    }
  }

  &--wide {
    .about-welcome__title {
      line-height: 48px;
      @media (max-width: $tablet) {
        line-height: 38px;
      }
      @media (max-width: $mobile) {
        line-height: 28px;
      }
    }

    .about-welcome__info {
      max-width: 1002px;
    }
  }

  &__btn {
    margin-top: 40px;
    @media (max-width: $tablet) {
      margin-top: 30px;
    }
    @media (max-width: $mobile) {
      margin-top: 24px;
    }
  }

  &__num {
    display: flex;
    flex-direction: column;
    max-width: 150px;
    height: auto;
    @media (max-width: $mobileMedium) {
      margin-bottom: 12px;
    }

    p {
      font-size: 16px;
      line-height: 16px;
      margin-bottom: auto;
      @media (max-width: $mobileMedium) {
        font-size: 14px;
        line-height: 16px;
      }
    }

    span {
      font-size: 24px;
      line-height: 34px;
      font-weight: 600;
    }
  }

  &__picture {
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
}

.cubes-info {
  margin: 80px 0 57px;
  @media (max-width: $tabletS) {
    margin: 74px 0 74px;
  }
  @media (max-width: $mobile) {
    margin: 50px 0;
  }

  &__wrapper {
    display: flex;
    @media (max-width: $tablet) {
      flex-direction: column-reverse;
    }
  }

  &__photos {
    position: relative;
    width: calc(60% - 100px);
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: auto 2fr;
    grid-gap: 20px;
    height: 600px;
    @media (max-width: 1500px) {
      height: 40vw;
      width: calc(60% - 80px);
      min-height: 500px;
    }
    @media (max-width: 1400px) {
      width: calc(60% - 60px);
    }
    @media (max-width: $tablet) {
      width: 100%;
    }
    @media (max-width: $mobile) {
      display: flex;
      flex-direction: column;
      height: unset;
      min-height: unset;
    }

  }

  &__first {
    position: relative;
    grid-row: 1/4;
    padding-top: 35px;
    padding-left: 30px;
    cursor: pointer;
    @media (max-width: $tabletS) {
      padding-top: 28px;
      padding-left: 23px;
    }
    @media (max-width: $mobile) {
      grid-column: 1/3;
      grid-row: 1;
      padding-top: 12px;
      padding-left: 12px;
      height: 76vw;
    }

    .play-icon {
      transform: translate(calc(-50% - 15px), calc(-50% - 15px));
    }

    &:hover {
      .play-icon {
        transform: translate(calc(-50% - 15px), calc(-50% - 15px)) scale(1.1);
      }
    }

    &::before {
      content: '';
      position: absolute;
      z-index: -1;
      left: 0;
      top: 0;
      width: 85%;
      height: 85%;
      background-color: #F6F6F6;
      border-radius: 16px;
      @media (max-width: $mobile) {
        height: 70%;
      }
    }

    &::after {
      content: '';
      position: absolute;
      z-index: -2;
      top: 20%;
      left: 30px;
      bottom: 0;
      right: 0;
      background-color: #eeeded;
      border-radius: 16px;
      box-shadow: -10px -7px 38px 30px #ADAEB06B;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 16px;
      overflow: hidden;
    }
  }

  &__label {
    grid-row: 1;
    grid-column: 2;
    border-radius: 16px;
    overflow: hidden;
    box-shadow: 0px 10px 10px 0px #ADAEB06B;
    padding: 20px;
    @media (max-width: $tabletS) {
      padding: 15px 20px;
    }
    @media (max-width: $mobile) {
      grid-row: 2;
      grid-column: 1/3;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__photo {
    grid-row: 2/4;
    grid-column: 2;
    border-radius: 16px;
    overflow: hidden;
    @media (max-width: $mobile) {
      display: none;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 40%;
    flex-shrink: 0;
    margin-left: 100px;
    @media (max-width: 1500px) {
      margin-left: 80px;
    }
    @media (max-width: 1400px) {
      margin-left: 60px;
    }
    @media (max-width: $tablet) {
      margin-left: 0;
      width: 100%;
      margin-bottom: 32px;
    }

    p {
      margin-bottom: 1em;
    }
  }
}

.cubes-label {
  &__title {
    font-size: 18px;
    font-weight: 500;
    line-height: 21.6px;
    margin-bottom: 10px;
    @media (max-width: $tabletS) {
      font-size: 16px;
      line-height: 19px;
      margin-bottom: 8px;
    }
  }

  &__text {
    font-size: 16px;
    font-weight: 400;
    line-height: 22.72px;
    margin-bottom: 16px;
  }
}

.play-icon {
  position: absolute;
  cursor: pointer;
  width: 70px;
  height: 70px;
  left: 53%;
  top: 50%;
  transform: translate(-50%, -50%);
  transform-origin: center; /*    */
  border-radius: 50%;
  backdrop-filter: blur(19.600000381469727px);
  background: linear-gradient(180.09deg, rgba(255, 255, 255, 0) 14.22%, rgba(255, 255, 255, 0.5) 64.35%);
  transition: transform 0.2s ease-in-out;

  &::after {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='18' height='20' viewBox='0 0 18 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M17.112 8.70761C17.9381 9.18422 17.9381 10.3764 17.112 10.853L2.83022 19.0934C2.00458 19.5697 0.972823 18.9739 0.972823 18.0207L0.972824 1.53998C0.972824 0.586764 2.00458 -0.00910976 2.83022 0.46727L17.112 8.70761Z' fill='white'/%3e%3c/svg%3e ");
    width: 18px;
    height: 20px;
    display: inline-block;
  }

  &:hover {
    transform: translate(-50%, -50%) scale(1.1);
  }
}

.features {
  margin: 50px 0 100px;
  @media (max-width: $tablet) {
    margin: 70px 0 70px;
  }

  &__title {
    @media (max-width: $mobile) {
      margin-bottom: 16px;
    }
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
    margin: -12px;
    @media (max-width: $tablet) {
      margin: 0;
      flex-wrap: nowrap;
      overflow-x: auto;
      margin-right: calc(50% - 50vw);
      margin-left: calc(50% - 50vw);
      padding-left: 40px;
    }
    @media (max-width: $mobileMedium) {
      padding-left: 16px;
    }
  }

  .feature-item {
    width: calc(25% - 24px);
    margin: 12px;
    border: 1px solid #EAEBED;
    padding: 32px 24px;
    border-radius: 10px;
    @media (max-width: $tablet) {
      width: 380px;
      flex-shrink: 0;
      padding: 24px;
      margin: 8px;
    }
    @media (max-width: $mobile) {
      width: 300px;
      padding: 16px 16px 24px;
    }

    &__icon {
      display: flex;
      margin-bottom: 20px;
    }

    &__title {
      font-size: 20px;
      font-weight: 500;
      line-height: 28.9px;
      margin-bottom: 12px;
      @media (max-width: $tablet) {
        font-size: 18px;
        line-height: 20.9px;
      }
    }

    &__text {
      font-size: 18px;
      font-weight: 400;
      line-height: 22px;
      @media (max-width: $tablet) {
        font-size: 16px;
      }
    }
  }


}

.sertificats {
  margin: 108px 0 120px;
  @media (max-width: $tablet) {
    margin: 74px 0;
  }
  @media (max-width: $mobile) {
    margin: 50px 0;
  }

  .swiper {
    @media (max-width: $tablet) {
      padding-left: 40px;
    }
    @media (max-width: $mobileMedium) {
      padding-left: 16px;
    }
  }

  &__title {
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      margin-bottom: 24px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 20px;
    }
  }

  .swiper-slide {
    background-color: #F6F6F6;
    padding: 30px;
    border-radius: 16px;
    overflow: hidden;
    cursor: pointer;
    @include hover-effect;
    @media (max-width: $tablet) {
      width: 180px;
    }
    @media (max-width: $mobile) {
      width: 24vw;
      padding: 20px;
      min-width: 180px;
    }

    img {
      width: 100%;
    }
  }

  &__slider {
    position: relative;
    @media (max-width: $tablet) {
      margin: 0 calc(50% - 50vw);
    }
  }

  &__nav {
    display: flex;
    position: absolute;
    top: -22px;
    transform: translateY(-100%);
    right: 0;
    @media (max-width: $tablet) {
      display: none;
    }


    .slider-prev {
      margin-right: 16px;
    }
  }
}

.slider-mini-prev,
.slider-mini-next {
  position: relative;
  width: 40px;
  height: 40px;
  background-color: #fff;
  border-radius: 50%;
  cursor: pointer;

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 7px;
    height: 12px;
  }
}

.slider-mini-prev {
  &::before {
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='7' height='12' viewBox='0 0 7 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M0.46967 5.46967C0.176777 5.76256 0.176777 6.23744 0.46967 6.53033L5.24264 11.3033C5.53553 11.5962 6.01041 11.5962 6.3033 11.3033C6.59619 11.0104 6.59619 10.5355 6.3033 10.2426L2.06066 6L6.3033 1.75736C6.59619 1.46447 6.59619 0.989593 6.3033 0.6967C6.01041 0.403806 5.53553 0.403806 5.24264 0.696699L0.46967 5.46967ZM2 5.25L1 5.25L1 6.75L2 6.75L2 5.25Z' fill='%236F73EE'/%3e%3c/svg%3e ");
  }
}

.slider-mini-next {
  &::before {
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='7' height='12' viewBox='0 0 7 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M6.53033 5.46967C6.82322 5.76256 6.82322 6.23744 6.53033 6.53033L1.75736 11.3033C1.46447 11.5962 0.989593 11.5962 0.6967 11.3033C0.403806 11.0104 0.403806 10.5355 0.6967 10.2426L4.93934 6L0.696699 1.75736C0.403805 1.46447 0.403805 0.989593 0.696699 0.6967C0.989592 0.403806 1.46447 0.403806 1.75736 0.696699L6.53033 5.46967ZM5 5.25L6 5.25L6 6.75L5 6.75L5 5.25Z' fill='%236F73EE'/%3e%3c/svg%3e ");
  }
}

.slider-prev,
.slider-next {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  cursor: pointer;
  background-color: #6F73EE;
  transition: opacity .3s, background-color .3s;

  svg path {
    fill: #fff;
  }

  &:hover {
    opacity: .7;
  }

  &.swiper-button-disabled {
    background-color: #fff;
    border: 1px solid var(--Text, #171717);
    opacity: 1;
    cursor: default;

    svg path {
      fill: #282739;
    }
  }
}

.tabs {
  display: flex;
  margin: -6px;
  overflow-x: auto;

  &__item {
    margin: 6px;
    font-size: 20px;
    font-weight: 400;
    line-height: 26px;
    letter-spacing: 0.008em;
    padding: 12px 14px;
    background-color: #F4F7FB;
    color: #5D6C7B;
    border-radius: 10px;
    @media (max-width: $mobile) {
      padding: 11px;
      font-size: 18px;
      font-weight: 400;
      line-height: 23.4px;
    }

    &:not(.tabs__item--active) {
      @include hover-effect;
    }

    &[data-active],
    &--active {
      background-color: #6F73EE;
      color: #fff;
      cursor: auto;
    }
  }
}

.blog {
  margin-bottom: 120px;
  @media (max-width: $tablet) {
    margin-bottom: 76px;
  }
  @media (max-width: $mobile) {
    margin-bottom: 50px;
  }

  &__title {
    margin-bottom: 30px;
    @media (max-width: $mobile) {
      margin-bottom: 20px;
    }
  }

  &__content {
    margin-top: 46px;
    @media (max-width: $tabletS) {
      margin-top: 20px;
    }
  }

  &__items {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px -32px;
    @media (max-width: $tabletS) {
      margin: -8px;
    }
    @media (max-width: $mobileMedium) {
      margin: 0 0 -16px;
    }

    .blog-item {
      width: calc(25% - 20px);
      margin: 0 10px 32px;
      @media (max-width: 1280px) {
        width: calc(33% - 20px);
      }
      @media (max-width: $tabletS) {
        width: calc(50% - 20px);
        margin: 8px;
      }
      @media (max-width: $mobileMedium) {
        width: 100%;
        margin: 0 0 16px;
      }

      &--main {
        width: 100%;

        .l-label {
          left: 24px;
          top: 24px;
          @media (max-width: $mobile) {
            left: 12px;
            top: 12px;
          }
        }
      }
    }
  }

  &__more {
    display: flex;
    justify-content: center;
    margin-top: 40px;
  }
}

.video-container {
  position: absolute;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.play-button {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(15.96px);
  @media (max-width: $mobile) {
    width: 50px;
    height: 50px;
  }

  &::after {
    content: "";
