.blog-item {
  display: flex;
  flex-direction: column;
  border-radius: 16px;
  overflow: hidden;

  a {
    @include hover-effect;
  }

  &__photo {
    position: relative;
    display: inline-block;
    width: 100%;
    height: 263px;
    @media (max-width: 1700px) {
