    }
  }

  &__label {

  }

  &--main {

    .btn-arrow {
      @media (max-width: $mobileMedium) {
        color: $darkBlue;
        span {
          border-bottom-color: $darkBlue;
        }

        &::after {
          background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M11.2249 4.55778C11.1079 4.67497 11.0421 4.83382 11.0421 4.99945C11.0421 5.16508 11.1079 5.32393 11.2249 5.44112L15.1582 9.37445H3.33323C3.16747 9.37445 3.0085 9.4403 2.89129 9.55751C2.77408 9.67472 2.70823 9.83369 2.70823 9.99945C2.70823 10.1652 2.77408 10.3242 2.89129 10.4414C3.0085 10.5586 3.16747 10.6244 3.33323 10.6244H15.1582L11.2249 14.5578C11.1635 14.615 11.1142 14.684 11.0801 14.7607C11.0459 14.8373 11.0276 14.9201 11.0261 15.004C11.0246 15.0879 11.04 15.1713 11.0715 15.2491C11.1029 15.3269 11.1497 15.3976 11.209 15.457C11.2684 15.5163 11.3391 15.5631 11.4169 15.5946C11.4947 15.626 11.5781 15.6414 11.662 15.6399C11.7459 15.6385 11.8287 15.6201 11.9053 15.5859C11.982 15.5518 12.051 15.5025 12.1082 15.4411L17.1082 10.4411C17.2253 10.3239 17.291 10.1651 17.291 9.99945C17.291 9.83382 17.2253 9.67497 17.1082 9.55778L12.1082 4.55778C11.991 4.44074 11.8322 4.375 11.6666 4.375C11.5009 4.375 11.3421 4.44074 11.2249 4.55778Z' fill='%23282739'/%3e%3c/svg%3e ");
        }
      }
    }

    @media (min-width: 576px) {

      position: relative;
      border-radius: 24px;
      overflow: hidden;

      .blog-item {
        &__label {
          top: 24px;
          left: 24px;
        }

        &__photo {
          position: relative;
          height: 450px;
          @media (max-width: 1700px) {
            height: 26vw;
            min-height: 360px;
          }

          &::after {
            content: '';
            position: absolute;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000a6 100%);
            width: 100%;
            height: 50%;
            bottom: 0;
            left: 0;
            display: inline-block;
            z-index: 1;
          }

          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }

        &__title {
          font-size: 32px;
          font-weight: 600;
          line-height: 38.08px;
          margin-bottom: 12px;
          @media (max-width: $tabletS) {
            font-size: 20px;
            font-weight: 600;
            line-height: 23.8px;
            margin-bottom: 8px;
          }
        }

        &__description {
          font-size: 18px;
          font-weight: 400;
          line-height: 26.01px;
          margin-bottom: 12px;
          @media (max-width: $tabletS) {
            font-size: 16px;
            font-weight: 400;
            line-height: 23.08px;
          }
        }

        &__info {
          position: absolute;
          z-index: 2;
          left: 24px;
          bottom: 24px;
          padding: 0;
          color: #fff;
          max-width: 540px;
          background-color: unset;
          @media (max-width: $mobile) {
            left: 0;
            bottom: 0;
            padding: 16px;
          }
        }
      }
    }
  }
}

.blog-detail {
  margin-bottom: 100px;
  @media (max-width: $tablet) {
    margin-bottom: 74px;
  }
  @media (max-width: $mobile) {
    margin-bottom: 50px;
  }
}

.blog-welcome {
  position: relative;
  border-radius: 24px;
  overflow: hidden;
  padding: 20px 20px 30px;
  height: 425px;
  color: #fff;
  background-size: cover;
  background-position: center;
  @media (max-width: 1500px) {
    height: 32vw;
  }
  @media (max-width: $tablet) {
    height: 41vw;
  }
  @media (max-width: $tabletS) {
    padding: 16px 20px;
    min-height: 289px;
  }
  @media (max-width: $mobile) {
    padding: 16px;
  }

  &::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000e5 100%);
    height: 50%;
  }

  .container-narrow {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    height: 100%;
  }

  &__title {
    max-width: 700px;
    text-transform: uppercase;
    font-size: 36px;
    font-weight: 600;
    line-height: 50px;
    @media (max-width: $tablet) {
      font-size: 24px;
      line-height: 28.56px;
    }
    @media (max-width: $mobile) {
      font-size: 18px;
      line-height: 26.64px;
    }
  }
}


.blog-detail {
  &__welcome {
    margin-bottom: 50px;
    @media (max-width: $tablet) {
      margin-bottom: 32px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 20px;
    }
  }

  &__text {
    p {
      margin-bottom: 1em;
    }
  }

  &__slider {
    width: 100%;
    overflow: hidden;
    margin: 50px 0;
    @media (max-width: $tablet) {
      margin: 32px 0;
    }

    .swiper {
      width: 100%;
      height: 650px;
      border-radius: 24px;
      overflow: hidden;
      @media (max-width: 1500px) {
        height: 40vw;
        min-height: 289px;
      }
    }

    .swiper-slide {
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
    }

    .slider-mini-next,
    .slider-mini-prev {
      position: absolute;
      z-index: 2;
    }

    .slider-mini-next {
      top: 50%;
      right: 40px;
      transform: translateY(-50%);
      @media (max-width: $tablet) {
        right: 24px;
      }
      @media (max-width: $mobile) {
        right: 20px;
      }
    }

    .slider-mini-prev {
      top: 50%;
      left: 40px;
      transform: translateY(-50%);
      @media (max-width: $tablet) {
        left: 24px;
      }
      @media (max-width: $mobile) {
        left: 20px;
      }
    }
  }
}

.other-news {
  margin: 100px 0 120px;
  @media (max-width: $tablet) {
    margin: 74px 0;
  }
  @media (max-width: $mobile) {
    margin: 50px 0;
  }

  &__slider {
    .swiper {
      @media (max-width: 1440px) {
        margin-right: calc(50% - 50vw);
        margin-left: calc(50% - 50vw);
        padding-left: calc(50vw - 50%);
        padding-right: calc(50vw - 50%);
      }
    }


    .blog-item {
      @media (max-width: 1440px) {
        width: 408px;
      }
      @media (max-width: $tabletS) {
        width: 51vw;
        min-width: 290px;
      }

      &__photo {
        @media (max-width: $mobile) {
          height: 40vw;
        }
      }
    }
  }
}


.contacts {
  &__title {
    margin-bottom: 36px;
    @media (max-width: $mobile) {
      margin-bottom: 20px;
    }
  }
}


.contacts-block {
  display: none;
  padding-top: 50px;
  @media (max-width: $tablet) {
    padding-top: 32px;
  }

  &[data-active] {
    display: block;
  }

  &__map {
    margin: 0 calc(50% - 50vw);

    div {
      width: 100%;

      iframe {
        width: 100%;
      }
    }
  }

  &__items {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 18px;
    @media (max-width: $tablet) {
      margin-bottom: 0;
    }
  }

  &__item {
    max-width: 25%;
    margin-bottom: 32px;

    &:nth-child(4n - 1),
    &:nth-child(4n - 2),
    &:nth-child(4n), {
      margin-left: auto;
      margin-right: auto;
      @media (max-width: $mobile) {
        margin-left: 0;
        margin-right: 0;
      }
    }

    &--triple {
      @media (max-width: $tablet) {
        max-width: unset;
        width: 100%;
        & + .contacts-block__item {
          margin-left: 0;
        }
      }
    }

    @media (max-width: $mobile) {
      max-width: unset;
      width: 100%;
    }

    h3 {
      font-size: 24px;
      font-weight: 500;
      line-height: 28.8px;
      margin-bottom: 12px;
      @media (max-width: $tabletS) {
        font-size: 20px;
        line-height: 24px;
      }
    }

    div, a {
      font-size: 20px;
      line-height: 30px;
      margin-bottom: 4px;
      color: #2D2D2D;
      @media (max-width: $tabletS) {
        font-size: 18px;
        line-height: 27px;
      }

    }

    p {
      font-size: 16px;
      line-height: 24px;
      color: #6B7280;
      margin-top: 4px;
    }

    .file-link {
      color: #6B7280;
    }
  }
}


.catalog-sort {
  display: flex;
  position: relative;
  font-size: 16px;
  line-height: 24px;
  min-width: 180px;
  white-space: nowrap;

  &__current {
    position: relative;
    display: flex;
    align-items: center;
    cursor: pointer;

    &::after {
      content: '';
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='10' height='6' viewBox='0 0 10 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cg clip-path='url(%23clip0_441_24063)'%3e%3cg clip-path='url(%23clip1_441_24063)'%3e%3cpath d='M1 0.5L5 4.5L9 0.5' stroke='%23282739'/%3e%3c/g%3e%3c/g%3e%3cdefs%3e%3cclipPath id='clip0_441_24063'%3e%3crect width='10' height='6' fill='white'/%3e%3c/clipPath%3e%3cclipPath id='clip1_441_24063'%3e%3crect width='10' height='6' fill='white'/%3e%3c/clipPath%3e%3c/defs%3e%3c/svg%3e ");
      width: 10px;
      height: 6px;
      margin-left: 5px;
      flex-shrink: 0;
      transition: transform .3s;
      transform-origin: center;
    }
  }

  &__body {
    position: absolute;
    top: calc(100% + 3px);
    left: 0;
    width: 100%;
    background-color: #fff;
    box-shadow: 0px 4px 5px -2px #18274B0F;
    border-radius: 15px;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
    transform: translateY(-5px);
    transition: opacity .3s, transform .3s;

    &[data-active] {
      opacity: 1;
      z-index: 3;
      visibility: visible;
      transform: translateY(0);

      & + .catalog-sort__current {
        &::after {
          transform: rotate(180deg);
        }
      }
    }
  }

  &__item-to-choose {
    padding: 8px 12px;
    cursor: pointer;
    @include hover-effect;

    &[data-active] {
      color: #6f73ee;
      font-weight: 500;
      cursor: auto;

      &:hover {
        opacity: 1;
      }
    }
  }
}

.catalog-filter {
  display: flex;
  align-items: center;
  @media (max-width: $mobileMedium) {
    width: 100%;
  }

  &__btn {
    display: inline-flex;
    align-items: center;
    padding: 10px 20px;
    font-size: 14px;
    line-height: 21px;
    border-radius: 45px;
    border: 1px solid #C4C4C4;
    cursor: pointer;
    @media (max-width: $mobileMedium) {
      margin-right: 8px;
    }

    &.tumbler-btn,
    &--dark {
      padding: 10px 12px;
    }

    span {
      display: flex;
      justify-content: center;
      align-items: center;
    }

    span + span {
      margin-left: 4px;
    }

    &--dark {
      background: #404040;
      color: #fff;
      font-weight: 500;
      border: 1px solid #404040;
    }
  }

  &__buttons {
    display: flex;
    margin: 0 4px;
    @media (max-width: $tablet) {
      .catalog-filter__btn:not(.tumbler-btn) {
        display: none;
      }
    }

    .catalog-filter__btn {
      margin: 0 4px;
    }
  }

  &__count {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    margin-left: 10px;
    color: #6B717A;
    @media (max-width: $tablet) {
      display: none;
    }
  }
}

.catalog-subcategory {
  display: flex;
  align-items: center;
  padding: 20px;
  background-color: #F6F6F6;
  cursor: pointer;
  border-radius: 16px;
  @media (max-width: $tablet) {
    padding: 0;
    background-color: unset;
    white-space: nowrap;
  }

  span {
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: .5;
  }

  &--active {
    span {
      opacity: 1;
    }
  }


  .catalog-subcategory__icon {
    flex-shrink: 0;
    margin-right: 24px;
    max-width: 60px;
    @media (max-width: $tablet) {
      display: none;
    }

    svg {
      max-width: 100%;
    }
  }

}


.catalog {
  margin-bottom: 150px;
  @media (max-width: $tablet) {
    margin-bottom: 74px;
  }
  @media (max-width: $mobile) {
    margin-bottom: 50px;
  }

  &__title {
    margin-bottom: 28px;
    @media (max-width: $tablet) {
      margin-bottom: 20px;
    }
  }

  &__sort {
    @media (max-width: $mobileMedium) {
      display: none;
    }
  }

  &__more {
    display: flex;
    justify-content: center;

    .btn {
      min-width: 280px;
    }
  }

  &__sub {
    display: flex;
    overflow-x: auto;
    margin: -10px calc(50% - 50vw) 22px;

    .catalog-subcategory {
      margin: 10px;
      min-width: 260px;
      @media (max-width: $tablet) {
        min-width: unset;
      }
    }

    .simplebar-content {
      display: flex;
      white-space: nowrap;
    }


    .catalog-subcategory {
      white-space: normal;
      @media (max-width: $tablet){
        white-space: nowrap;
      }
    }
  }

  &__bar {
    display: flex;
    justify-content: space-between;
  }

  &__items {
    display: flex;
    flex-wrap: wrap;
    padding: 16px 0 50px;
    @media (max-width: $tablet) {
      padding: 16px 0 30px;
    }

    .catalog-card {
      width: 25%;
      margin-bottom: 20px;
      @media (max-width: $laptop) {
        width: 33%;
      }
      @media (max-width: $tabletS) {
        width: 50%;
      }
      @media (max-width: $mobileMedium) {
        margin-bottom: 16px;
      }
    }

    .catalog-hint {
      width: 50%;
      margin-bottom: 20px;
      padding: 0 10px;
      @media (max-width: $laptop) {
        width: 66%;
      }
      @media (max-width: $tabletS) {
        width: 100%;
      }
      @media (max-width: $mobileMedium) {
        margin-bottom: 16px;
        padding: 0;
      }

      img {
        width: 100%;
        height: auto;
        object-fit: contain;
      }
    }
  }
}


.catalog-card {
  display: flex;
  flex-direction: column;
  padding: 10px;
  background-color: #fff;
  transition: box-shadow .3s;
  border-radius: 12px;
  @media (max-width: $mobileMedium) {
    padding: 6px;
  }

  &:hover {
    box-shadow: 0px 13.5px 18.4px 0px #C3C4C633,
    0px 11.04px 56.43px 0px #C3C4C61F,
    0px 29.44px 46.62px 0px #C3C4C624;
  }

  &__pagination {

    &.swiper-pagination-bullets {
      position: absolute;
      z-index: 2;
      bottom: 12px;
      left: 50%;
      transform: translateX(-50%);
      display: flex;
      justify-content: center;
    }


    .swiper-pagination-bullet {
      background: #D6D6D6;
      opacity: 1;
      width: 5px;
      height: 5px;
      transition: width .3s;
    }

    .swiper-pagination-bullet.swiper-pagination-bullet-active {
      background-color: $darkBlue;
      width: 20px;
      border-radius: 18px;
    }
  }

  &__photo {
    position: relative;
    height: 380px;
    margin-bottom: 12px;
