.filter-body {
  display: flex;
  flex-direction: column;
  position: fixed;
  right: 0;
  top: 0;
  bottom: 0;
  width: 540px;
  z-index: 100;
  padding: 20px 24px 24px;
  background-color: #fff;
  overflow-y: auto;
  transform: translateX(100%);
  transition: transform .3s;

  @media (max-width: $tablet) {
    width: 495px;
  }
  @media (max-width: $mobileMedium) {
    width: 100%;
  }

  &.opened {
    transform: translateX(0);
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__title {
    font-weight: 500;
    font-size: 40px;
    line-height: 57.8px;
    @media (max-width: $tablet) {
      font-size: 32px;
      line-height: 46.24px;
    }
    @media (max-width: $mobile) {
      font-size: 28px;
      line-height: 40.46px;
    }
  }

  &__close {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    cursor: pointer;
    @include hover-effect;
  }

  &__box {
    padding: 20px 0;
    border-bottom: 1px solid #C4C4C4
  }

  &__checks {
    display: flex;
    flex-direction: column;

    label {
      display: flex;
      align-items: center;
      padding: 4px 0;
      cursor: pointer;

      span {
        position: relative;
        display: inline-flex;
        align-items: center;
        font-weight: 400;
        font-size: 16px;
        line-height: 26px;
        letter-spacing: 0.3px;

        &::before {
          content: '';
          flex-shrink: 0;
          display: inline-block;
          width: 20px;
          height: 20px;
          border-radius: 4px;
          border: 1px solid #B5B9C2;
          margin-right: 12px;
        }
      }

      input {
        display: none;

        &:checked {
          & + span {
            &::after {
              content: '';
              position: absolute;
              display: inline-block;
              width: 13px;
              height: 13px;
              background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='27' height='23' viewBox='0 0 27 23' xmlns='http://www.w3.org/2000/svg'%3e%3cpath class='svg-line-check' d='M1.97 11.94L10.03 20 25.217 2' fill='none' fill-rule='evenodd' stroke='%23ffffff' stroke-width='3' stroke-linecap='round'%3e%3c/path%3e%3c/svg%3e");
              left: 4px;
              top: 50%;
              transform: translateY(-50%);
              background-size: contain;
              background-repeat: no-repeat;
            }

            &::before {
              background-color: $darkBlue;
            }
          }
        }
      }
    }

    &--pictures {
      label {
        span {
          &::before {
            border-radius: 50%;
            background-color: var(--color);
          }
        }

        input:checked {
          & + span {
            &::after {
              content: none;
            }

            &::before {
              background-color: var(--color);
              border: 2px solid $darkBlue;
            }
          }
        }
      }
    }

    .closed {
      display: none;
    }

    &.opened {
      .closed {
        display: block;
      }
    }
  }

  &__list {
    overflow-y: auto;
    padding-right: 10px;
  }

  &__subtitle {
    margin-bottom: 16px;
    font-weight: 500;
    font-size: 18px;
    line-height: 26.01px;
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 23.12px;
    }
  }

  &__more {
    display: inline-flex;
    align-items: center;
    order: 2;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.3px;
    margin-right: 12px;
    color: #6B717A;
    cursor: pointer;
    @include hover-effect;
    margin-top: 12px;

    span {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 8px;
    }
  }

  &__buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 32px;
    margin-top: auto;
  }

  &__submit {
    width: 100%;
  }

  &__clear {
    margin-top: 20px;
  }
}

