  }

  &__title-block {
    max-width: 30%;
    @media (max-width: 1300px) {
      max-width: 40%;
    }
    @media (max-width: $tablet) {
      max-width: unset;
      width: 100%;
    }
  }

  &__features {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    max-width: 30%;
    @media (max-width: 1300px) {
      max-width: 40%;
    }
    @media (max-width: $tablet) {
      width: 100%;
      margin: 0 -20px -40px;
      max-width: 600px;
    }
    @media (max-width: $mobileMedium) {
      max-width: 100%;
      margin: 0 0 -14px;
    }

    .icon-feature {
      width: calc(50% - 20px);
      flex-shrink: 0;
      @media (max-width: $tablet) {
        width: calc(50% - 80px);
        margin: 0 20px 40px;
      }
      @media (max-width: $mobileMedium) {
        width: 100%;
        margin: 0 0 14px;
      }
    }
