}

.icon-feature {
  display: flex;
  align-items: center;

  &__svg {
    display: flex;
    margin-right: 16px;
    @media (max-width: $mobile) {
      margin-right: 12px;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 18px;
    line-height: 26.01px;
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 23.12px;
    }
  }
}

.galery-slider {
  margin: 120px 0;
  @media (max-width: $tablet) {
    margin: 74px 0;
  }
  @media (max-width: $mobile) {
    margin: 50px 0;
  }

  &__wrapper {
    display: flex;
    cursor: pointer;
    overflow: hidden;
    white-space: nowrap;
    height: 770px;
    @media (max-width: 1599px) {
      height: 40vw;
    }
    @media (max-width: $tablet) {
      height: 56vw;
    }
    @media (max-width: $mobile) {
      height: 80vw;
    }
  }

  &__slide {
    width: 100vw;
    flex-shrink: 0;
    display: grid;
    grid-template-columns: 1.7fr 0.6fr 2fr 1.6fr 1fr 0.6fr 1fr 1.6fr;
    grid-template-rows: 1fr 0.4fr 0.8fr 3.3fr 0.9fr 1.4fr 1.3fr 0.3fr 0.6fr;
    grid-gap: 18px;
    height: 100%;
    cursor: pointer;
    margin-right: 18px;
    @media (max-width: $tablet) {
      width: 166.66vw;
    }
    @media (max-width: $mobile) {
      width: 250vw;
      grid-template-rows: 1fr 0.3fr 0.6fr 2.5fr 0.7fr 1fr 1fr 0.2fr 0.4fr;
      grid-gap: 6px;
      margin-right: 6px;
    }
  }

  &__title {
    text-align: center;
    margin-bottom: 40px;
    @media (max-width: $tablet) {
      margin-bottom: 32px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 20px;
    }
  }

  &__item {
    border-radius: 16px;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &--1 {
      grid-column: 1;
      grid-row: 1/5;
    }

    &--2 {
      grid-column: 1/3;
      grid-row: 5/10;
    }

    &--3 {
      grid-column: 2/4;
      grid-row: 2/5;
    }

    &--4 {
      grid-column: 3;
      grid-row: 5/8;
    }

    &--5 {
      grid-column: 4;
      grid-row: 4/7;
    }

    &--6 {
      grid-column: 5/7;
      grid-row: 3/6;
    }

    &--7 {
      grid-column: 5;
      grid-row: 6/10;
    }

    &--8 {
      grid-column: 7/9;
      grid-row: 1/6;
    }

    &--9 {
      grid-column: 6/8;
      grid-row: 6/8;
    }
  }

}

.to-tabs {
  margin: 100px 0;
  @media (max-width: $tablet) {
    margin: 75px 0;
  }
  @media (max-width: $mobile) {
    margin: 50px 0;
  }

  &__wrapper {
    display: flex;
  }

  &__picture {
    max-width: 50%;
    flex-shrink: 0;
    margin-left: 40px;
    @media (max-width: 1500px) {
      margin-left: 20px;
    }
    @media (max-width: $tablet) {
      display: none;
    }

    img {
      width: calc(100%);
      height: calc(100% + 60px);
      object-fit: contain;
    }
  }

  &__content {
    height: auto;
  }

}

.info-tabs {
  display: flex;
  flex-direction: column;
  width: 100%;

  &__top {
    display: flex;
    overflow: hidden;
    overflow-x: auto;
    width: 100%;
  }

  &__blocks {
    position: relative;
    top: -1px;
    flex-grow: 1;
    height: 100%;
  }

  &__block {
    display: none;
    height: 100%;
    padding: 60px 64px;
    background-color: #F5F5F5;
    border-radius: 0 16px 16px 16px;
    @media (max-width: 1500px) {
      padding: 40px;
    }
    @media (max-width: $mobile) {
      padding: 32px 24px;
    }

    &[data-active] {
      display: block;
    }

    .btn {
      margin-top: 20px;
      min-width: 240px;
      @media (max-width: $tablet) {
        margin-top: 16px;
      }
      @media (max-width: $mobile) {
        margin-top: 12px;
      }
    }
  }

  &__btn-item {
    position: relative;
    display: inline-block;
    font-weight: 500;
    font-size: 18px;
    line-height: 22.5px;
    height: 53px;
    padding: 16px 0;
    margin: 0 32px;
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 20px;
    }

    &[data-active] {
      background-color: #F5F5F5;

      &::after {
        content: '';
        width: 32px;
        height: 53px;
        display: inline-block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translateX(100%);
        bottom: 0;
        background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='32' height='55' viewBox='0 0 32 55' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M0 0C7.78017 0 14.7044 4.93373 17.2448 12.2875L32 55H0V0Z' fill='%23F6F6F6'/%3e%3c/svg%3e ");
      }

      &::before {
        content: '';
        width: 32px;
        height: 53px;
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
        transform: translateX(-100%);
        bottom: 0;
        background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='32' height='53' viewBox='0 0 32 53' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M32 0C24.2022 0 17.2445 4.89705 14.613 12.2374L0 53H32V0Z' fill='%23F6F6F6'/%3e%3c/svg%3e ");
      }

    }
  }


  p {
    margin-bottom: 1em
  }

  ul {
    margin: 1.8em 0 1em;

    li {
      position: relative;
      display: flex;
      align-items: center;

      &::before {
        content: '';
        display: inline-block;
        width: 8px;
        height: 8px;
        background: #6F73EE;
        border-radius: 50%;
        margin-right: 12px;
      }

      & + li {
        margin-top: 16px;
      }
    }
  }
}

.four-blocks {
  margin: 100px 0;
  @media (max-width: $tablet) {
    margin: 74px 0;
  }
  @media (max-width: $mobile) {
    margin: 50px 0;
  }

  &__content {
    display: grid;
    grid-gap: 20px;
    grid-template-rows: auto auto;
    grid-template-columns: 1fr 1fr 1fr;
    @media (max-width: $tablet) {
      grid-template-columns: 1fr 1fr;
      grid-gap: 12px;
      min-height: unset;
      height: unset;
    }
  }

  &__sub {
    font-weight: 500;
    font-size: 26px;
    line-height: 37.57px;
    margin-bottom: 12px;
    @media (max-width: $tablet) {
      font-weight: 500;
      font-size: 18px;
      line-height: 26.01px;
      margin-bottom: 8px;
    }
  }

  &__text {
    opacity: .8;
  }

  &__icon {
    position: relative;
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
    margin: auto -32px -32px;
    min-height: 100px;
    @media (max-width: $tablet) {
      margin: auto -24px -24px;
    }
    @media (max-width: $mobile) {
      margin: auto -16px -16px;
    }

    img {
      width: auto;
      height: 100%;
      max-width: 100%;
      object-fit: contain;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    background-color: #f6f6f6;
    border-radius: 16px;
    overflow: hidden;
    padding: 32px;
    @media (max-width: $tablet) {
      padding: 24px;
      height: 38vw;
    }
    @media (max-width: $mobile) {
      height: 50vw;
      min-height: 300px;
      padding: 16px;
    }
    @media (max-width: $mobileMedium) {
      height: 74vw;
    }

    &--first {
      grid-row: 1/3;
      grid-column: 1;

      img {
        object-position: bottom;
      }

      @media (max-width: $tablet) {
        grid-row: 1;
        img {
          object-fit: cover;
          width: 100%;
          object-position: top;
        }
      }
      @media (max-width: $mobile) {
        grid-row: 1;
        grid-column: 1/3;
      }
    }

    &--second {
      grid-row: 1/3;
      grid-column: 2;

      img {
        object-position: bottom;
      }

      @media (max-width: $tablet) {
        grid-row: 1;
        grid-column: 2;
        img {
          width: 67%;
          object-fit: cover;
          object-position: top;
        }
      }
      @media (max-width: $mobile) {
        grid-row: 2;
        grid-column: 1/3;
      }
    }

    &--third {
      grid-row: 1;
      grid-column: 3;

      img {
        max-width: 330px;
        object-position: bottom;
      }

      @media (max-width: 1600px) {
        img {
          max-width: 20vw;
        }
      }
      @media (max-width: $tablet) {
        grid-row: 2;
        grid-column: 1;
        img {
          max-width: 33vw;
          object-position: bottom;
        }
      }
      @media (max-width: $mobile) {
        grid-row: 3;
        grid-column: 1/3;
        img {
          max-width: 68vw;
          margin: 0 auto;
        }
      }

      .four-blocks__icon {
        margin-right: -12px;
      }
    }

    &--fourth {
      grid-row: 2;
      grid-column: 3;

      img {
        max-width: 220px;
        object-position: bottom;
      }

      @media (max-width: 1600px) {
        img {
          max-width: 15vw;
        }
      }

      @media (max-width: $tablet) {
        grid-column: 2;
        img {
          max-width: 20vw;
        }
      }
      @media (max-width: $mobile) {
        grid-row: 4;
        grid-column: 1/3;
        img {
          max-width: 42vw;
          margin: 0 auto;
        }
      }

      .four-blocks__icon {
        margin-right: -12px;
      }
    }
  }
}

.steps {
  margin: 100px 0;
  @media (max-width: $tablet) {
    margin: 74px 0;
  }
  @media (max-width: $mobile) {
    margin: 50px 0;
  }

  &__btn {
    display: flex;
    justify-content: center;
    margin-top: 80px;
    @media (max-width: $tablet) {
      margin-top: 17px;
    }
    @media (max-width: $mobile) {
      margin-top: 0;
    }
  }

  &__title {
    text-align: center;
    margin-bottom: 100px;
    @media (max-width: $tablet) {
      margin-bottom: 32px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 24px;
    }
  }

  &__content {
    display: flex;
    margin: 0 -25px;
    @media (max-width: $tablet) {
      flex-wrap: wrap;
    }
    @media (max-width: $mobileMedium) {
      margin: 0;
    }

    .step-item {
      width: 20%;
      flex-shrink: 0;
      padding: 0 25px;
      @media (max-width: $tablet) {
        width: 33%;
        margin-bottom: 24px;
      }
      @media (max-width: $mobileMedium) {
        width: 100%;
      }

      & + .step-item {
        position: relative;

        &::before {
          content: '';
          position: absolute;
          height: 0;
          border: 1px dashed #8F8F98;
          top: 50px;
          left: 0%;
          width: 40%;
          transform: translateX(-50%);
          @media (max-width: $tablet) {
            top: 32px;
            width: 66%;
          }
          @media (max-width: $mobileMedium) {
            left: 50%;
            bottom: calc(100%);
            width: 0;
            height: 20px;
            top: -2px;
            transform: translateY(-100%);
          }
        }
      }
    }
  }
}

.step-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  &__number {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 1px solid #6F73EE;
    font-weight: 400;
    font-size: 32px;
    line-height: 46.24px;
    margin-bottom: 36px;
    @media (max-width: $tablet) {
      width: 65px;
      height: 65px;
      font-size: 24px;
      line-height: 34.68px;
      margin-bottom: 16px;
    }
    @media (max-width: $mobileMedium) {
      width: 50px;
      height: 50px;
      margin-bottom: 12px;
    }

    &--icon {
      background-color: #6F73EE;
      @media (max-width: $tablet) {
        padding: 11px;
      }
    }

  }
}

.partners {
  margin: 100px 0;
  @media (max-width: $tablet) {
    margin: 74px 0;
  }
  @media (max-width: $mobile) {
    margin: 50px 0;
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
    margin: -10px;
    @media (max-width: $tablet) {
      margin: -6px;
    }
  }

  &__item {
    flex-shrink: 0;
    width: calc(20% - 20px);
    margin: 10px;
    background: #F6F6F6;
    border-radius: 16px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    filter: grayscale(100%); /*  - */
    transition: filter 0.2s ease-in-out; /*   */
    cursor: pointer;
    padding: 20px;
    @media (max-width: $tablet) {
      width: calc(25% - 12px);
      padding: 16px;
      margin: 6px;
    }
    @media (max-width: $mobile) {
      width: calc(50% - 12px);
    }

    &:hover {
      filter: grayscale(0%);
    }

    img {
      width: 100%;
      max-width: 180px;
    }
  }
