.delivery {
  margin-bottom: 70px;
  @media (max-width: $tablet) {
    margin-bottom: 50px;
  }
  @media (max-width: $mobile) {
    margin-bottom: 36px;
  }

  &__title {
    margin-bottom: 50px;
    @media (max-width: $tablet) {
      margin-bottom: 32px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 20px;
    }
  }

  &__sub {
    margin-bottom: 16px;
  }

  .delivery__tabs {
  }

  &__block {
    display: none;

    &[data-active] {
      display: block;
    }
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 50px 0;
    @media (max-width: $tablet) {
      padding: 24px 0;
    }

    &--line {
      @media (max-width: $tablet) {
        flex-direction: column;
        .delivery__inner-row + .delivery__inner-row {
          margin-top: 20px;
        }
      }
    }

    h3 {
      font-weight: 500;
      font-size: 26px;
      line-height: 31.2px;
      margin-bottom: 20px;
      @media (max-width: $tablet) {
        font-size: 20px;
        line-height: 24px;
        margin-bottom: 8px;
      }
