      width: calc(50% - 15px);
      font-size: 18px;
      line-height: 27px;
    }
    @media (max-width: $mobile) {
      width: 100%;
    }

    &--sb {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    &--bottom {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      @media (max-width: $tablet) {
        position: relative;
