.basket {
  margin-bottom: 120px;
  @media (max-width: $tablet) {
    margin-bottom: 75px;
  }
  @media (max-width: $mobile) {
    margin-bottom: 50px;
  }

  &__title {
    margin-bottom: 10px;
  }

  &__content {
    display: flex;
    @media (max-width: $tablet) {
      flex-direction: column;
    }
  }

  &__checkout {
    position: relative;
    flex-shrink: 0;
    width: 420px;
    margin-left: 120px;
    @media (max-width: 1500px) {
      margin-left: 80px;
      width: 400px;
    }
    @media (max-width: 1300px) {
      margin-left: 40px;
      width: 320px;
    }
    @media (max-width: $tablet) {
      width: 100%;
      margin-left: 0;
      margin-top: 8px;
    }
  }

  &__items {
    flex-grow: 1;
  }
}

.basket-item {
  width: 100%;
  padding: 24px 0;
  @media (max-width: $tablet) {
    padding: 20px 0;
  }
  @media (max-width: $mobile) {
    padding: 12px 0;
  }

  & + .basket-item {
    border-top: 1px solid #F1F3F9;
  }

  &__wrapper {
    display: flex;
    @media (max-width: $mobile) {
      flex-wrap: wrap;
    }
  }

  &__prices {
    width: 120px;
    @media (max-width: $tablet) {
      width: 110px;
    }
    @media (max-width: $mobile) {
      display: flex;
      align-items: center;
      width: unset;
    }
  }

  &__price {
    font-weight: 600;
    font-size: 24px;
    line-height: 28.8px;
    margin-bottom: 4px;
    white-space: nowrap;
    @media (max-width: $tablet) {
      font-weight: 600;
      font-size: 18px;
      line-height: 21.6px;
    }
    @media (max-width: $mobile) {
      font-size: 14px;
      line-height: 21px;
      margin-bottom: 0;
      margin-right: 8px;
    }
  }

  &__sale {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  &__old-price {
    font-weight: 500;
    font-size: 16px;
    line-height: 23.12px;
    color: #5D6C7B;
    text-decoration: line-through;
    white-space: nowrap;

    @media (max-width: $tablet) {
      font-size: 16px;
      line-height: 23.12px;
    }
    @media (max-width: $mobile) {
      font-size: 14px;
      line-height: 21px;
    }
  }

  &__discount {
    display: inline-block;
    padding: 1px 4px;
    font-weight: 500;
    font-size: 12px;
    line-height: 17.34px;
    background: #E31235;
    color: #fff;
    border-radius: 4px;
    margin-left: 6px;
  }

  &__logic {
    display: flex;
    align-items: center;
    @media (max-width: $mobile) {
      order: 1;
      margin-top: 12px;
    }
  }

  &__quantity {
    margin-right: 32px;
    @media (max-width: $tablet) {
      margin-right: 24px;
    }
    @media (max-width: $mobile) {
      margin-right: 8px;
      margin-left: calc(12vw + 8px);
    }
  }

  &__photo {
    flex-shrink: 0;
    display: inline-block;
    width: 120px;
    height: 120px;
    border-radius: 12px;
    overflow: hidden;
    background-color: #f6f6f6;
    margin-right: 20px;
    @include hover-effect;
    @media (max-width: $tablet) {
      width: 70px;
      height: 70px;
      margin-right: 12px;
    }
    @media (max-width: $mobile) {
      width: 12vw;
      height: 12vw;
      margin-right: 8px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: center;
    }
  }

  &__info {
    display: flex;
    align-items: center;
    margin-right: auto;
    padding-right: 10px;
    @media (max-width: $mobile) {
      width: 90%;
    }
  }

  &__name {
    font-weight: 500;
    font-size: 20px;
    line-height: 26px;
    max-width: 380px;
    @include hover-effect;
    @media (max-width: $tablet) {
      font-size: 16px;
      line-height: 23.04px;
    }
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 40px;
    flex-shrink: 0;
    @media (max-width: $mobile) {
      margin-left: 0;
      width: 10%;
      justify-content: flex-end;
      align-items: flex-start;
    }
  }

  &__del-btn {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 36px;
    height: 36px;
    flex-shrink: 0;
    cursor: pointer;
    @include hover-effect;
    @media (max-width: $tablet) {
      width: 24px;
      height: 24px;
    }
  }
}

.basket-checkout {
  position: sticky;
  top: var(--header-height);
  height: auto;
  padding: 24px;
  background: #F6F6F6;
  border-radius: 16px;
  @media (max-width: $mobile) {
    padding: 16px;
  }

  &__title-block {
    padding-bottom: 20px;
    border-bottom: 1px solid #28273930;
    @media (max-width: $tablet) {
      padding-bottom: 12px;
    }

    p {
      font-weight: 400;
      font-size: 16px;
      line-height: 23.12px;
      margin-bottom: 2px;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 32px;
    line-height: 36.24px;
    @media (max-width: $tablet) {
      font-size: 24px;
      line-height: 34.68px;
    }
  }

  &__list {
    padding: 20px 0;
    border-bottom: 1px solid #28273930;
    @media (max-width: $tablet) {
      padding: 12px 0;
    }

    li {
      display: flex;
      justify-content: space-between;

      & + li {
        margin-top: 16px;
        @media (max-width: $tablet) {
          margin-top: 8px;
        }
      }

      span {
        &:first-child {
          font-weight: 400;
          font-size: 18px;
          line-height: 26.01px;
          @media (max-width: $tablet) {
            font-size: 16px;
            line-height: 23.12px;
          }
        }

        &:last-child {
          font-weight: 600;
          font-size: 18px;
          line-height: 26.01px;
          white-space: nowrap;
          @media (max-width: $tablet) {
            font-size: 16px;
            line-height: 23.12px;
          }
        }
      }
    }
  }

  &__footer {
    padding-top: 20px;
  }

  &__submit {
    width: 100%;
    margin-bottom: 16px;
  }

  &__agree {
    input[type='checkbox'] + span {
      display: inline-block;
      font-weight: 400;
      font-size: 16px;
      line-height: 20.39px;
      color: $darkBlue;
      opacity: 1;

      &::before {
        background-color: #fff;
      }

      a {
        display: inline-block;
        color: #6F73EE;
        @include hover-effect;
      }
    }
  }
}

.order-status {
  height: 100%;

  .container {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 120px 0;
    @media (max-width: $mobile) {
      margin: 80px 0;
    }
  }

  &__title {
    margin-bottom: 8px;
    @media (max-width: $tablet) {
      margin-bottom: 4px;
    }
  }

  &__subtitle {
    color: #6B717A;
  }

  &__button {
    margin-top: 32px;
    @media (max-width: $tablet) {
      margin-top: 20px;
    }
  }
}

