.order {
  margin-bottom: 120px;
  @media (max-width: $tablet) {
    margin-bottom: 75px;
  }
  @media (max-width: $mobile) {
    margin-bottom: 50px;
  }

  &__title {
    margin-bottom: 10px;
  }

  &__subtitle {
    font-weight: 400;
    font-size: 16px;
    line-height: 23.12px;
    color: #6B717A;
  }

  &__content {
    display: flex;
    @media (max-width: 1200px) {
      flex-direction: column;
    }
  }

  &__steps {
    flex-grow: 1;
  }

  &__checkout {
    position: relative;
    flex-shrink: 0;
    width: 420px;
    margin-left: 228px;
    @media (max-width: 1500px) {
      margin-left: 80px;
      width: 400px;
    }
    @media (max-width: 1300px) {
      margin-left: 40px;
      width: 320px;
    }
    @media (max-width: $tablet) {
      width: 100%;
      margin-left: 0;
      margin-top: 8px;
    }
  }

  &__deliveries,
  &__payments {
    display: flex;
    flex-wrap: wrap;
    margin: -10px;
    @media (max-width: $tablet) {
      margin: -6px;
    }
    @media (max-width: $mobileMedium) {
      margin: 0 0 -12px;
    }

    .delivery-item,
    .payment-item {
      width: calc(33% - 20px);
      margin: 10px;
      @media (max-width: $tablet) {
        width: calc(50% - 12px);
        margin: 6px;
      }
      @media (max-width: $mobileMedium) {
        width: 100%;
        margin: 0 0 12px;
      }
    }
  }
}

.delivery-item,
.payment-item {
  cursor: pointer;
  position: relative;

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__label {
    background: #75D14A;
    padding: 3px 9.5px;
    font-weight: 500;
    font-size: 12px;
    line-height: 17.34px;
    right: 20px;
    top: 20px;
    color: #fff;
    border-radius: 5px;
  }

  &__info {
    position: relative;
    padding: 20px 16px;
    background: #F6F6F6;
    height: 100%;
    border-radius: 16px;
    padding-left: 48px;
    border: 1px solid #F6F6F6;
    @media (max-width: 1300px) {
      padding: 16px;
      padding-left: 44px;
    }
    @media (max-width: $tablet) {
      padding: 12px;
      padding-left: 40px;
    }

    &::before {
      content: '';
      position: absolute;
      width: 16px;
      height: 16px;
      display: inline-block;
      border: 1px solid #EAEBED;
      background-color: #fff;
      border-radius: 50%;
      left: 20px;
      @media (max-width: 1300px) {
        top: 22px;
      }
      @media (max-width: $tablet) {
        top: 19px;
      }
    }
  }

  &__title {
    font-weight: 500;
    font-size: 18px;
    line-height: 26.01px;
    margin-bottom: 12px;
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 24.07px;

    ul {
      li {
        position: relative;

        &::before {
          content: '';
          background: #F3A712;
          display: inline-block;
          width: 5px;
          height: 5px;
          margin-right: 9px;
        }
      }
    }
  }

  input {
    display: none;

    &:checked + .delivery-item__info,
    &:checked + .payment-item__info {
      border: 1px solid #6F73EE;
      background-color: #fff;

      &::before {
        border-color: #6F73EE;
      }

      &::after {
        content: '';
        position: absolute;
        width: 8px;
        height: 8px;
        display: inline-block;
        background-color: #6F73EE;
        border-radius: 50%;
        left: 24px;
        top: 24px;
        @media (max-width: 1300px) {
          top: 26px;
          left: 24px;
        }
        @media (max-width: $tablet) {
          top: 23px;
          left: 24px;
        }
      }
    }
  }
}


.order-step {
  padding: 25px 0;
  @media (max-width: $mobile) {
    padding: 16px 0;
  }

  &__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 23.8px;
    margin-bottom: 28px;
    @media (max-width: $mobile) {
      font-size: 18px;
      line-height: 21.42px;
      margin-bottom: 16px;
    }

    span {
      color: #6F73EE;
    }
  }

  .four-fields {
    display: flex;
    margin: 0 -10px;

    .form-input {
      width: calc(25% - 20px);
      margin: 0 10px;
    }
  }

  &__form {
    max-width: 680px;

    .double-fields {
      @media (max-width: $mobile) {
        flex-wrap: wrap;
        .form-input {
          width: 100%;
        }
      }
    }

    .four-fields {
      @media (max-width: $mobile) {
        flex-wrap: wrap;
        .form-input {
          width: calc(50% - 20px);
        }
      }
    }

    label {
      color: $darkBlue;

      span {
        color: #FF3E0E;
      }
    }
  }

  .form-additional-checkbox {
    position: absolute;
    left: 0;
    top: calc(100% + 6px);
    width: 100%;

    input + span {
      color: $darkBlue;
      opacity: 1;
    }
  }
}

