      }
      @media (max-width: $mobile) {
        width: 50vw;
      }

      img {
        width: 100%;
      }
    }
  }

  &__title {
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      margin-bottom: 24px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 16px;
    }
  }


  &__slider {
    position: relative;
    @media (max-width: $tablet) {
      margin: 0 calc(50% - 50vw);
    }
  }

  &__nav {
    display: flex;
    position: absolute;
    top: -22px;
    transform: translateY(-100%);
    right: 0;
    @media (max-width: $tablet) {
      display: none;
    }


    .slider-prev {
      margin-right: 16px;
    }
  }
}


