.area-darken {
  position: relative;
  z-index: 3;
  opacity: 0.6;
  pointer-events: none;
}

.modals {
  margin-bottom: 0
}

.modals--active {
  position: fixed;
  overflow: hidden;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #00000099;
  -webkit-backdrop-filter: blur(2px);
  backdrop-filter: blur(2px);
  z-index: 1000;
  cursor: pointer;
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  max-width: calc(100% - 24px);
  max-height: calc(100% - 24px);
  min-width: 300px;
  overflow-y: auto;
  background: #ffffff;
  border-radius: 15px;
  transform: translateX(-50%) translateY(-50%);
  transition: all .3s;
}

@media (max-width: $mobileMedium) {
  .modal {
    width: calc(100% - 30px);
  }
}

.modal__close-button {
  position: absolute;
  top: 12px;
  right: 12px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 40px;
  height: 40px;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.modal__close-button::after {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 24px;
  height: 24px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M6.2636 6.2636C6.61508 5.91213 7.18492 5.91213 7.5364 6.2636L12 10.7272L16.4636 6.2636C16.8151 5.91213 17.3849 5.91213 17.7364 6.2636C18.0879 6.61508 18.0879 7.18492 17.7364 7.5364L13.2728 12L17.7364 16.4636C18.0879 16.8151 18.0879 17.3849 17.7364 17.7364C17.3849 18.0879 16.8151 18.0879 16.4636 17.7364L12 13.2728L7.5364 17.7364C7.18492 18.0879 6.61508 18.0879 6.2636 17.7364C5.91213 17.3849 5.91213 16.8151 6.2636 16.4636L10.7272 12L6.2636 7.5364C5.91213 7.18492 5.91213 6.61508 6.2636 6.2636Z' fill='%236B717A'/%3e%3c/svg%3e ");
  background-size: contain;
  content: "";
  z-index: 4;
}

@media (max-width: 768px) {
  .modal__close-button {
    top: 12px;
    right: 12px;
    width: 16px;
    height: 16px;
  }
}

.modal[data-hidden] {
  opacity: 0;
  visibility: hidden;
  z-index: -1;
  transform: translate(-50%, -50%) scale(0.9);
  transition: transform .2s, opacity .05s linear .15s, z-index 0s ease .3s, visibility 0s ease .3s;
}



.modal-form {
  padding:24px;
  width: 556px;
  @media (max-width: $tablet) {
    width: 522px;
  }
  @media (max-width: $mobileMedium) {
    width: 100%;
  }

  &__picture{
    margin-bottom: 20px;
    img{
      max-width: 100%;
    }
  }

  &__title{
    font-weight: 500;
    font-size: 28px;
    line-height: 38px;
    text-align: center;
    margin-bottom: 8px;
    @media (max-width: $tablet){
      font-size: 24px;
      line-height: 38px;
      margin-bottom: 4px;
    }

    &--left{
      text-align: left;
    }
  }

  &__subtitle{
    font-size: 16px;
    line-height: 24px;
    text-align: center;
  }

  &__form{
    margin-top: 20px;
  }

  &--video{
    overflow: hidden;
    video{
      display: block;
      width: 100%;
      height: 100%;
    }
  }


  label {
    display: inline-block;
    margin-bottom: 6px;
    font-weight: 500;
  }

  .form-input {
    & + .form-input {
      margin-top: 16px;
    }
  }

  &__text{
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
  }

  .form{
    &__double{
      display: flex;
      justify-content: space-between;
      @media (max-width: $mobile){
        flex-direction: column;
      }

      .form-input{
        flex-shrink: 0;
        width: calc(50% - 6px);
        margin:0;
        @media (max-width: $mobile){
          width: 100%;
          & + .form-input{
            margin-top: 12px;
          }
        }
      }
    }
  }

  &__full-button{
    margin-top: 12px;
    .btn{
      width: 100%;
    }
  }


  .form-input__error-text {
    position: static;
  }

  &__buttons {
    display: flex;
    margin-top: 30px;

    .btn {
      & + .btn {
        margin-left: 12px;
      }

      @media (max-width: $mobileMedium) {
        flex-grow: 1;
        & + .btn {
          margin-left: 12px;
        }
      }
    }
  }
}

.result-modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  &__icon {
    display: flex;
    margin-bottom: 10px;
    justify-content: center;
  }

  &__title {
    font-size: 24px;
    line-height: 28px;
    font-weight: 600;
    margin-bottom: 15px;
  }

  &__desc {
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
  }
}

