.btn {
  position: relative;
  z-index: 1;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: $darkBlue;
  border: 1px solid $darkBlue;
  border-radius: 10px;
  padding: 13px 20px;
  cursor: pointer;
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
  text-align: center;
  transition: background-position .3s, color .3s, background-color .3s, border-color .3s;
  overflow: hidden;

  &--semitr {
    background: #FFFFFF1A;
  }

  &--wide {
    padding: 13px 70px;
  }

  .b-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
    @media (max-width: $mobile) {
      margin-right: 8px;
    }

    svg {
      path {
        transition: fill .3s;
      }
    }
  }


  &::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: -1;
    background: radial-gradient(50% 50% at 50% 50%, #8C88CC 0%, #6F73EE 68%);
    background-position: 50% 50%; /*   hover */
    color: #fff;
    opacity: 0;
    transition: opacity .3s;
  }

  &:hover {
    color: #fff;
    border: 1px solid #8C88CC;

    .b-icon {
      svg {
        path {
          fill: #fff;
        }
      }
    }

    &::before {
      opacity: 1;
    }
  }

  &:active {
    background-color: #282739;
    color: #fff;
    border-color:#6F73EE;

    &:before{
      background: radial-gradient(50% 50% at 50% 50%, #6F73EE 0%, #6F73EE 68%);
    }

    .b-icon {
      svg {
        path {
          fill: #fff;
        }
      }
    }
  }

  &--filled {
    background-color: $orange;
    color: #fff;

    &:hover {
      background-color: transparent;
      color: $orange;
    }
  }

  &--blue {
    background-color: #6F73EE;
    border-color: #6F73EE;
    color: #fff;
    transition: background-color 0s;

    &:active {
      border-color: #696DDC;
      background-color: #696DDC;
    }

    &::before {
      background: radial-gradient(50% 50% at 50% 50%, #9092EB 0.5%, #6F73EE 100%);
      transition: opacity .3s;
    }

    &:hover {
      transition: background-color .3s;
      border-color: #6F73EE;
    }

  }

  &--light {
    border: 1px solid #FFFFFF;
    color: #ffff;
    background-color: #ffffff14;

    &:hover {
      background-color: #fff;
      color: $darkBlue;
      border: 1px solid #FFFFFF;
    }

    &:active {
      background-color: #6F73EE;
      color: #fff;
      border: 1px solid #6F73EE;
    }

    &:before {
      content: none;
    }
  }

  &--mini {
    padding: 4.5px 30px;
  }
}

