.tumbler-btn {
  input {
    display: none;

    &:checked {
      & + .tumbler-btn__text {
        &::before {
          background-color: $darkBlue;
        }

        &::after {
          left: 20px;
        }
      }
    }
  }

  &__text {
    position: relative;

    &::before {
      content: "";
      width: 40px;
      height: 25px;
      background: #C4C4C4;
      border-radius: 20px;
      transition: background-color .3s;
      margin-right: 4px;
    }

    &::after {
      content: '';
      position: absolute;
      display: inline-block;
      background-color: #fff;
      width: 15px;
      height: 15px;
      border-radius: 50%;
      left: 6px;
      transition: left .3s;
    }
  }
}

.btn-underline {
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0.3px;
  color: #6B717A;
  border-bottom: 1px solid #6B717A;
  @include hover-effect;
}

