  position: relative;
  display: inline-flex;
  align-items: center;
  background: #F3A712;
  color: #fff;
  font-weight: 500;
  font-size: 16px;
  line-height: 23.12px;
  padding: 6px 12px;
  border-radius: 8px;
  @media (max-width: $mobile){
    padding:4px;
  }

  &::before {
    content: '';
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='23' height='23' viewBox='0 0 23 23' fill='none' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3e%3crect x='0.296875' y='0.0546875' width='22.7027' height='22.7027' fill='url(%23pattern0_367_4381)'/%3e%3cdefs%3e%3cpattern id='pattern0_367_4381' patternContentUnits='objectBoundingBox' width='1' height='1'%3e%3cuse xlink:href='%23image0_367_4381' transform='scale(0.00694444)'/%3e%3c/pattern%3e%3cimage id='image0_367_4381' width='144' height='144' xlink:href='data:image/png;base64,'/%3e%3c/defs%3e%3c/svg%3e ");
    display: inline-block;
    width: 23px;
    height: 23px;
    margin-right: 8px;
  }
}

