}

.btn-icon {
  display: inline-flex;
  align-items: center;

  @include hover-effect;

  &__pic {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 36px;
    height: 36px;
    margin-right: 14px;
    border-radius:50%;
    background: #F5F5F6;
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #2D2D2D;
  }
}