.good-detail {
  position: relative;

  &__title {
    margin-bottom: 16px;
    @media (max-width: $mobile) {
      padding-right: 36px;
    }
  }

  &__social {
    display: flex;
    align-items: center;

    .share {
      @media (max-width: $mobile) {
        position: absolute;
        top: 20px;
        right: 20px;
      }
    }

    .share {
      margin-left: 16px;
    }
  }

  &__stars {
    margin-right: 16px;
  }

  .reviews-label {
    margin-right: 8px;
  }

  &__visual {
    grid-column: 1;
    grid-row: 1/3;
    @media (max-width: 1240px) {
      grid-column: 1/4;
      grid-row: 1;
    }
  }

  &__social {
    margin-bottom: 16px;
  }

  &__photos {
    display: flex;
    height: 511px;
    @media (max-width: 1600px) {
      height: 37vw;
    }
    @media (max-width: 1240px) {
      height: 49vw;
    }
    @media (max-width: $mobile) {
      height: auto;
      flex-direction: column-reverse;
    }
  }

  &__props {
    grid-column: 2;
    grid-row: 1/3;
    margin-left: auto;
    margin-right: auto;
    padding: 40px 20px 0;
    @media (max-width: 1240px) {
      grid-column: 1/4;
      grid-row: 3;
      margin-left: 0;
      margin-right: 0;
    }
    @media (max-width: $tablet) {
      padding: 0;
    }
  }

  &__basket-actions {
    grid-column: 3;
    grid-row: 1;
    margin-bottom: 14px;
    @media (max-width: 1240px) {
      grid-column: 1/4;
      grid-row: 2;
    }
    @media (max-width: $tablet) {
      box-shadow: none;
      background: #f7f7f7;
    }
  }

  &__logic {
    grid-column: 3;
    grid-row: 2;
    @media (max-width: 1240px) {
      grid-column: 1/4;
      grid-row: 4;
    }
  }

  &__main {
    display: grid;
    grid-template-columns: 768px 1fr 25%;
    @media (max-width: 1600px) {
      grid-template-columns: 45% 1fr 25%;
    }
    @media (max-width: 1400px) {
      grid-template-columns: 45% 1fr 28%;
    }
    @media (max-width: 1240px) {
      grid-row-gap: 12px;
    }
  }

  &__status {
    position: relative;
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    line-height: 19.5px;
    letter-spacing: 0.49px;
    margin-bottom: 4px;

    &[data-status='available'] {
      &::before {
        background: #1EBB5A;
      }
    }

    &[data-status='not-available'] {
      &::before {
        background: #E31235;
      }
    }

    &::before {
      content: '';
      display: inline-block;
      width: 7px;
      height: 7px;
      border-radius: 50%;
      margin-right: 6px;
    }
  }

  &__prices {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 28px;
    @media (max-width: $tablet) {
      margin-bottom: 0;
    }
  }

  &__actual-price {
    font-weight: 600;
    font-size: 32px;
    line-height: 24.38px;
    white-space: nowrap;
    margin-top: 8px;
    @media (max-width: $mobile) {
      font-size: 28px;
      line-height: 24.38px;
    }
  }

  &__old-price {
    font-weight: 400;
    font-size: 18px;
    line-height: 19.5px;
    letter-spacing: 0.49px;
    text-align: right;
    text-decoration: line-through;
    color: #404040;
    opacity: .6;
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 19.5px;
      letter-spacing: 0.49px;
    }
  }

  &__buttons {
    display: flex;
    flex-wrap: wrap;
    @media (max-width: $tablet) {
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      box-shadow: 0px 4px 46.2px 0px #6E777C40;
      background: #fff;
      z-index: 4;
      padding: 16px 40px;
    }
    @media (max-width: $mobile) {
      padding: 16px;
      .btn {
        font-size: 16px;
        line-height: 23.12px;
      }
    }

    .buy-btn {
      width: 100%;
    }
  }

  &__buy {
    width: 100%;
    margin-bottom: 12px;
    @media (max-width: $tablet) {
      margin-bottom: 8px;
    }
  }

  &__two-btns {
    display: flex;
    justify-content: space-between;
    width: 100%;

    button {
      width: calc(50% - 4px);
    }
  }

  &__discount {
    position: relative;
    display: inline-flex;
    align-items: center;
    white-space: nowrap;
    height: 32px;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: #767676;
    padding: 4px 7px 4px 15px;
    margin-left: 16px;
    @media (max-width: $mobile) {
      font-size: 10.67px;
      line-height: 18.3px;
    }

    &::before {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background-size: 100% 100%;
      background-repeat: no-repeat;
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='59' height='32' viewBox='0 0 59 32' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath opacity='0.5' d='M1.28069 13.6564L1.29023 13.6435L1.29893 13.6301L8.71402 2.17345C9.4777 1.15505 10.6924 0.536353 12.0222 0.5H53.5983C54.7042 0.5 55.6987 0.937252 56.4288 1.86605C57.1684 2.80673 57.67 4.29619 57.67 6.44643V27.4873C57.67 29.6728 55.8659 31.4946 53.5983 31.4946H12.0155C10.693 31.4946 9.47917 30.8801 8.71401 29.8597L1.29893 18.4032L1.29023 18.3897L1.28069 18.3769C0.239769 16.9742 0.239769 15.059 1.28069 13.6564ZM8.24075 18.9212C9.85551 18.9212 11.1645 17.6121 11.1645 15.9974C11.1645 14.3826 9.85551 13.0735 8.24075 13.0735C6.62599 13.0735 5.317 14.3826 5.317 15.9974C5.317 17.6121 6.62599 18.9212 8.24075 18.9212Z' stroke='%23767676'/%3e%3c/svg%3e ");
      width: 100%;
    }
  }

  &__discount-prices {
    margin-top: 8px;
    margin-left: 16px;
  }

  &__main-slider {
    position: relative;
    flex-grow: 1;
    width: calc(100% - 154px);
    @media (max-width: 1240px) {
      width: calc(100% - 16% - 12px);
    }
    @media (max-width: $mobile) {
      width: 100%;
      height: 70vw;
      margin-bottom: 4px;
    }

    .swiper {
      width: 100%;
      height: 100%;
    }

    video {
      width: 100%;
      height: 100%;
    }

    .swiper-slide {
      background: #F6F6F6;
      border-radius: 16px;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
  }

  &__side-slider {
    height: 100%;
    flex-shrink: 0;
    width: 142px;
    margin-right: 12px;
    @media (max-width: 1240px) {
      width: 16%;
    }
    @media (max-width: $mobile) {
      width: 100%;
      margin-right: 0;
    }

    .swiper {
      height: 100%;
    }

    .swiper-slide {
      position: relative;
      height: 123px;
      border-radius: 16px;
      overflow: hidden;
      cursor: pointer;
      @media (max-width: 1240px) {
        height: 12vw;
        border-radius: 12px;
      }
      @media (max-width: $mobile) {
        height: 17vw;
        width: 20vw;
        border-radius: 8px;
      }

      .play-button {
        width: 56px;
        height: 56px;
        @media (max-width: 1240px) {
          width: 42px;
          height: 42px;
          &::after {
            border-left: 11px solid white;
            border-top: 7px solid transparent;
            border-bottom: 7px solid transparent;
          }
        }
        @media (max-width: $mobile) {
          width: 27px;
          height: 27px;
        }
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      .video-container {
        background: #F6F6F6;
        border-radius: 16px;

        .play-button {
          backdrop-filter: blur(15.960000038146973px);
        }
      }

      video {
        width: 100%;
        height: 100%;
      }
    }
  }

  &__feature-title {
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    margin-bottom: 14px;
  }

  &__some-features {
    @media (max-width: 1240px) {
      display: none;
    }

    li {
      display: flex;
      justify-content: space-between;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;

      & + li {
        margin-top: 8px;
      }

      span:first-child {
        color: #747883;
      }
    }
  }

  &__to-all-features {
    display: inline-block;
    font-weight: 400;
    font-size: 16px;
    line-height: 19.2px;
    text-decoration: underline;
    margin-top: 13px;
    cursor: pointer;
    @include hover-effect;
  }

  &__basket-actions {
    border-radius: 22px;
    box-shadow: 0px 4px 27.4px 0px #A3ADB140;
    overflow: hidden;
    padding: 20px;
    @media (max-width: $tablet) {
      padding: 14px 16px;
      background-color: #f7f7f7;
      box-shadow: none;
    }
  }

  a.good-detail__grey-box {
    @include hover-effect;
  }

  &__grey-box {
    display: block;
    background-color: #f6f6f6;
    border-radius: 22px;
    overflow: hidden;
    padding: 14px 20px;
    margin-bottom: 10px;
    &[data-btn-modal]{
      cursor: pointer;
      @include hover-effect;
    }
    @media (max-width: $mobile) {
      margin-bottom: 8px;
    }
  }

  &__sub-title {
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;

    & + .good-detail__text {
      margin-top: 11px;
    }

    &--underline {
      text-decoration: underline;
    }

    .live-label {
      margin-right: 12px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;

    .narrow-box {
      max-width: 300px;
    }
  }

  &__space-between {
    display: flex;
    align-items: center;
    justify-content: space-between;

  }

  .arrow-right {
    position: relative;
    padding-right: 20px;

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 0;
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.20041 6.23478C8.48226 5.93125 8.95681 5.91367 9.26034 6.19553L12.7603 9.44553C12.9132 9.58743 13 9.78657 13 9.99512C13 10.2037 12.9132 10.4028 12.7603 10.5447L9.26034 13.7947C8.95681 14.0766 8.48226 14.059 8.20041 13.7555C7.91856 13.4519 7.93613 12.9774 8.23966 12.6955L11.1478 9.99512L8.23966 7.29472C7.93613 7.01286 7.91856 6.53832 8.20041 6.23478Z' fill='%23747883'/%3e%3c/svg%3e ");
      display: inline-block;
      width: 20px;
      height: 20px;
    }
  }

  &__info-list {
    li {
      display: flex;
      justify-content: space-between;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;

      & + li {
        margin-top: 16px;
      }

      @media (max-width: $mobile) {
        margin-right: -10px;
      }

      span:first-child {
        display: flex;
        font-weight: 500;
      }

      .dashed {
        flex-grow: 1;
        min-width: 20px;
        display: inline-block;
        position: relative;
        border-bottom: 1px dashed #050000;
        opacity: 30%;
        margin: 0 15px;
        @media (max-width: $mobile) {
          margin: 0 5px;
          min-width: 10px;
        }
      }

      span:last-child {
        color: #6B717A;
        text-align: right;
      }
    }
  }

  &__discount-label {
    position: absolute;
    left: -7px;
    top: 23px;
    z-index: 2;
    padding: 8px 11px;
    background-color: $red;
    font-size: 14px;
    line-height: 12px;
    color: #fff;
    border-radius: 2px;
    @media (max-width: $mobile){
      padding: 2px 4px;
    }

    &::before {
      content: '';
      position: absolute;
      left: 1px;
      top: -4px;
      display: inline-block;
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='6' height='4' viewBox='0 0 6 4' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M6 0V4H0L6 0Z' fill='%238C091F'/%3e%3c/svg%3e ");
      width: 6px;
      height: 4px;

    }
  }
}

.icon-box {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin-right: 8px;
}

.additional-price {
  position: relative;
  display: inline-flex;
  align-items: center;
  padding: 4px 8px;
  background: radial-gradient(50% 50% at 50% 50%, #777ACA 0%, #6F73EE 100%);
  color: #fff;
  border-radius: 8px;
  overflow: hidden;
  font-weight: 500;
  font-size: 16px;
  line-height: 23.12px;

  &::before {
    content: '';
    position: relative;
    top: -2px;
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='20' height='21' viewBox='0 0 20 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M14.7165 9.83333C14.5248 9.58333 14.2915 9.36667 14.0748 9.15C13.5165 8.65 12.8831 8.29167 12.3498 7.76667C11.1081 6.55 10.8331 4.54167 11.6248 3C10.8331 3.19167 10.1415 3.625 9.5498 4.1C7.39146 5.83333 6.54146 8.89167 7.55813 11.5167C7.59146 11.6 7.6248 11.6833 7.6248 11.7917C7.6248 11.975 7.4998 12.1417 7.33313 12.2083C7.14146 12.2917 6.94146 12.2417 6.78313 12.1083C6.73583 12.0687 6.69628 12.0207 6.66646 11.9667C5.7248 10.775 5.5748 9.06667 6.20813 7.7C4.81646 8.83333 4.05813 10.75 4.16646 12.5583C4.21646 12.975 4.26646 13.3917 4.40813 13.8083C4.5248 14.3083 4.7498 14.8083 4.9998 15.25C5.8998 16.6917 7.45813 17.725 9.13313 17.9333C10.9165 18.1583 12.8248 17.8333 14.1915 16.6C15.7165 15.2167 16.2498 13 15.4665 11.1L15.3581 10.8833C15.1831 10.5 14.7165 9.83333 14.7165 9.83333ZM12.0831 15.0833C11.8498 15.2833 11.4665 15.5 11.1665 15.5833C10.2331 15.9167 9.2998 15.45 8.7498 14.9C9.74146 14.6667 10.3331 13.9333 10.5081 13.1917C10.6498 12.525 10.3831 11.975 10.2748 11.3333C10.1748 10.7167 10.1915 10.1917 10.4165 9.61667C10.5748 9.93333 10.7415 10.25 10.9415 10.5C11.5831 11.3333 12.5915 11.7 12.8081 12.8333C12.8415 12.95 12.8581 13.0667 12.8581 13.1917C12.8831 13.875 12.5831 14.625 12.0831 15.0833Z' fill='white'/%3e%3c/svg%3e ");
    display: inline-block;
    width: 20px;
    height: 21px;
    margin-right: 8px;
  }
}

.buy-btn {
  position: relative;
  display: inline-flex;
  align-items: center;

  &::after {
    content: '';
    position: relative;
    z-index: 2;
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='27' height='26' viewBox='0 0 27 26' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M24.0834 24.725L22.5257 7.37756C22.4831 6.90381 22.0861 6.54094 21.6105 6.54094H18.3982V5.09214C18.3982 2.39203 16.2016 0.195312 13.5016 0.195312C10.8017 0.195312 8.60538 2.39203 8.60538 5.09214V6.54094H5.39086C4.91528 6.54094 4.51822 6.90381 4.47571 7.37756L2.91188 24.7933C2.88885 25.0503 2.97485 25.305 3.14893 25.4954C3.32302 25.6858 3.56914 25.7942 3.82702 25.7942H23.1745C23.1752 25.7942 23.1762 25.7942 23.1769 25.7942C23.6845 25.7942 24.0957 25.3828 24.0957 24.8754C24.0956 24.8243 24.0914 24.7739 24.0834 24.725ZM10.443 5.09214C10.443 3.40531 11.8151 2.03296 13.5017 2.03296C15.1884 2.03296 16.5607 3.40531 16.5607 5.09214V6.54094H10.443V5.09214ZM4.83209 23.9567L6.23091 8.37858H8.60538V10.0217C8.60538 10.5291 9.01665 10.9405 9.52421 10.9405C10.0318 10.9405 10.443 10.5291 10.443 10.0217V8.37858H16.5607V10.0217C16.5607 10.5291 16.9719 10.9405 17.4795 10.9405C17.987 10.9405 18.3983 10.5291 18.3983 10.0217V8.37858H20.7706L22.1694 23.9567H4.83209Z' fill='white'/%3e%3c/svg%3e ");
    width: 27px;
    height: 27px;
    display: inline-block;
    order: -1;
    margin-right: 12px;
  }
}

.props-box {
  margin-bottom: 24px;
  @media (max-width: $mobile) {
    margin-bottom: 12px;
  }

  &__label {
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 12px;
    color: #747883;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: -4px;

    li {
      margin: 4px;
    }
  }

  li {
    display: inline-block;
    padding: 12px;
    border-radius: 8px;
    overflow: hidden;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    border: 1px solid #D7DAE4;
    color: #747883;
    cursor: pointer;
    min-width: 46px;
    text-align: center;

    &.prop-active {
      color: #6F73EE;
      border-color: #6F73EE;
    }
  }
}

.buy-one-click {
  position: relative;
  display: flex;
  align-items: center;
}

.live-label {
  position: relative;
  display: inline-flex;
  align-items: center;
  border-radius: 22px;
  overflow: hidden;
  background: #EB425E;
  color: #fff;
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  padding: 1px 8px;

  &::before {
    content: '';
    display: inline-block;
    width: 4px;
    height: 4px;
    background-color: #fff;
    margin-right: 6px;
  }
}

.good-tabs {
  &__top {
    display: flex;
    width: 100%;
    overflow: hidden;
    overflow-x: auto;
    border-bottom: 2.28px solid #EAEBED;
  }

  &__block {
    display: none;

    &[data-active] {
      display: block;
    }
  }

  &__btn {
    display: inline-block;
    padding: 10px 20px;
    color: #6B717A;
    cursor: pointer;
    font-weight: 600;
    font-size: 18px;
    line-height: 26.01px;
    white-space: nowrap;
    @media (max-width: $tabletS) {
      font-weight: 500;
      font-size: 16px;
      line-height: 23.12px;
      padding: 10px 15px;
    }

    &:hover {
      @include hover-effect;
    }

    &[data-active] {
      position: relative;
      color: #6F73EE;
      cursor: auto;

      &:hover {
        opacity: 1;
      }

      &::after {
        content: '';
        position: absolute;
        bottom: -2px;
        left: 0;
        background-color: #6F73EE;
        height: 3px;
        width: 100%;
      }
    }
  }

  &__blocks {
    padding-top: 32px;
    padding-bottom: 32px;
    @media (max-width: $tablet) {
      padding-top: 24px;
      padding-bottom: 24px;
    }
    @media (max-width: $mobile) {
      padding-top: 16px;
      padding-bottom: 16px;
    }
  }
}

.good-features {
  padding-right: 40px;


  &__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 27px;
    margin-bottom: 20px;
    @media (max-width: $tablet) {
      font-size: 16px;
      line-height: 27px;
      margin-bottom: 16px;
    }
  }

  &__prop-name {
    font-weight: 400;
    font-size: 15px;
    line-height: 18px;
    color: #6B717A;
  }

  &__prop-value {
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
  }

  &__ul {
    @media (max-width: $tablet) {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    li {
      display: flex;
      justify-content: space-between;
      font-weight: 400;
      font-size: 16px;
      line-height: 18px;
      padding: 8px 0;
      @media (max-width: $tablet) {
        width: calc(50% - 16px);
        flex-shrink: 0;
      }
      @media (max-width: $tablet) {
        width: 100%;
      }

      & + li {

      }

      .dashed {
        flex-grow: 1;
        min-width: 20px;
        display: inline-block;
        position: relative;
        border-bottom: 1px dashed #050000;
        opacity: 30%;
        margin: 0 15px;
        @media (max-width: $mobile) {
          margin: 0 5px;
          min-width: 10px;
        }
      }
    }
  }
}


.description-block {
  &__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 27px;
    margin-bottom: 20px;
    @media (max-width: $tablet) {
      font-size: 16px;
      line-height: 27px;
      margin-bottom: 16px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
  }

  &__buttons {
    display: flex;
    margin: 2px -18px -18px;

    .btn-icon {
      margin: 18px;
    }
  }
}

.features-description {
  display: flex;
  @media (max-width: $tablet) {
    flex-direction: column;
  }

  .good-features {
    width: 480px;
    margin-right: 6%;
    @media (max-width: $tablet) {
      padding-right: 0;
      width: 100%;
      margin-bottom: 32px;
      margin-right: 0;
    }
    @media (max-width: $mobile) {
      margin-bottom: 24px;
    }
  }

  .description-block {
    max-width: 565px;
  }
}

.delivery-and-payment {
  display: flex;
  @media (max-width: $tablet) {
    flex-direction: column;
  }

  &__title {
    font-weight: 600;
    font-size: 20px;
    line-height: 28.9px;
    margin-bottom: 24px;
    @media (max-width: $tablet) {
      font-size: 18px;
      line-height: 26.01px;
      margin-bottom: 12px;
    }
  }

  &__left {
    width: 240px;
    margin-right: 10%;
    @media (max-width: $tablet) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 24px;
    }
  }

  &__right {
    max-width: 560px;
  }

  &__subtitle {
    font-weight: 500;
    font-size: 16px;
    line-height: 23.12px;
    margin-bottom: 8px;
  }

  &__list {
    li {
      display: flex;
      position: relative;
      font-weight: 400;
      font-size: 16px;
      line-height: 25px;


      &::before {
        position: relative;
        top: 8px;
        content: '';
        background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='8' height='9' viewBox='0 0 8 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M5.83013 1.03595C6.49679 0.651048 7.33013 1.13217 7.33013 1.90197L7.33013 7.09812C7.33013 7.86792 6.49679 8.34905 5.83013 7.96415L1.33013 5.36607C0.663461 4.98117 0.66346 4.01892 1.33013 3.63402L5.83013 1.03595Z' fill='%23EE685F'/%3e%3c/svg%3e ");
        width: 8px;
        height: 9px;
        display: inline-block;
        margin-right: 6px;
      }
    }
  }
}

.sertificats-block {
  &__items {
    display: flex;
    flex-wrap: wrap;
    margin: -10px;
  }

  &__item {
    width: calc((100% / 7) - 20px);
    margin: 10px;
    border-radius: 16px;
    overflow: hidden;
    padding: 30px;
    background-color: #f6f6f6;
    cursor: pointer;
    @include hover-effect;
    @media (max-width: 1440px) {
      width: calc((100% / 5) - 20px);
      padding: 20px;
    }
    @media (max-width: $tablet) {
      width: calc((100% / 4) - 20px);
      padding: 20px;
    }
    @media (max-width: $mobile) {
      width: calc((100% / 3) - 20px);
      padding: 16px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
}

.good-reviews {
  display: flex;
  @media (max-width: $tablet) {
    flex-direction: column;
  }

  &__content {
    max-width: 1196px;
    @media (max-width: $tablet) {
      width: 100%;
    }
  }

  &__leave-comment {
    @media (max-width: $tablet) {
      order: -1;
      width: 100%;
      margin-bottom: 24px;
    }
    @media (max-width: $mobileMedium){
      .btn{
        width: 100%;
      }
    }
  }

  &__items {
    margin-right: 30px;
    @media (max-width: $mobile) {
      margin-right: 0;
    }
  }

  &__empty {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 500px;
    text-align: center;
    margin: 0 auto;

    .good-reviews__btn {
      margin: 0 auto;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 24px;
    line-height: 34.68px;
    margin-bottom: 12px;
  }

  &__subtext {
    font-weight: 400;
    font-size: 18px;
    line-height: 26.01px;
    margin-bottom: 32px;
  }

  &__more {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 40px;
    @media (max-width: $mobileMedium) {
      margin-top: 24px;
    }

    .btn {
      min-width: 280px;
      @media (max-width: $mobileMedium) {
        width: 100%;
      }
    }

  }

  &__btn {
    height: 100%;
    margin-left: auto;


    .btn {
      min-width: 280px;

    }
  }
}

.good-review-item {
  display: flex;
  padding: 32px 0;
  border-bottom: 1px solid #EAEBED;
  @media (max-width: $tablet) {
    flex-direction: column;
    &:first-child {
      border-top: 1px solid #EAEBED;
    }
  }

  &__hidden {
    display: none;

    &[data-open] {
      display: block;
    }
  }

  &__more-text {
    text-decoration: underline;
    cursor: pointer;
    @include hover-effect;
  }

  &__profile {
    display: flex;
    align-items: center;
    height: 100%;
    flex-shrink: 0;
    width: 280px;
    margin-right: 50px;
    @media (max-width: 1440px) {
      width: 240px;
      margin-right: 40px;
    }
    @media (max-width: $tablet) {
      margin-bottom: 16px;
      width: 100%;
    }
    @media (max-width: $mobile) {
      margin-bottom: 12px;
    }
  }

  &__ava {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 56px;
    height: 56px;
    background-color: #f7f7f7;
    border-radius: 50%;
    font-weight: 500;
    font-size: 22px;
    line-height: 33px;
    color: #C4C4C4;
    margin-right: 20px;
    @media (max-width: $tabletS) {
      width: 36px;
      height: 36px;
      margin-right: 16px;
      font-weight: 500;
      font-size: 22px;
      line-height: 33px;
    }
    @media (max-width: $mobile) {
      width: 36px;
      height: 36px;
      font-size: 16px;
      line-height: 33px;
    }
  }

  &__personal {
    @media (max-width: $tabletS) {
      display: flex;
      align-items: center;
    }
  }

  &__name {
    font-weight: 500;
    font-size: 20px;
    line-height: 28.9px;
    margin-bottom: 2px;
    @media (max-width: $tabletS) {
      font-size: 18px;
      line-height: 26.01px;
    }
  }

  &__date {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: #6B717A;
    white-space: nowrap;
    @media (max-width: $tabletS) {
      margin-left: 8px;
    }
    @media (max-width: $mobile) {
      font-weight: 400;
      font-size: 14px;
      line-height: 24px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 26.01px;
    margin-bottom: 20px;
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 23.12px;
      margin-bottom: 12px;
    }
  }

  &__photos {
    display: flex;
    flex-wrap: wrap;
    margin: -7px;
  }

  &__photo,
  &__more {
    position: relative;
    border-radius: 5px;
    overflow: hidden;
    width: calc((100% / 3) - 14px);
    margin: 7px;

    &--hidden {
      display: none;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__more {
    position: relative;
    cursor: pointer;

    span {
      position: absolute;
      color: #fff;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 80%;
      text-align: center;
      font-weight: 500;
      font-size: 24px;
      line-height: 34.68px;
    }

    &:hover {
      &::before {
        opacity: .2;
      }
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: #000;
      opacity: .3;
      transition: opacity .3s;
    }
  }

}