.footer {

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(4, auto);
    padding: 70px 0;
    @media (max-width: 1240px) {
      grid-template-columns: repeat(3, auto);
    }
    @media (max-width: $tablet) {
      padding: 36px 0;
    }
    @media (max-width: $mobile) {
      padding:20px 0;
    }
    @media (max-width: $mobileMedium){
      grid-row-gap: 16px;
    }
  }

  &__logo-block {
    grid-column: 1;
    grid-row: 2;
    margin-bottom: 28px;
    @media (max-width: 1240px) {
      grid-column: 1/3;
      grid-row: 1;
    }
  }

  &__logo {
    display: inline-block;
    @include hover-effect;
  }

  &__socials {
    display: flex;
    align-items: center;
    grid-column: 1;
    grid-row: 3;
    margin: 0 -4px;
    @media (max-width: 1240px) {
      grid-column: 3;
      grid-row: 3;
    }
    @media (max-width: $mobileMedium){
      grid-column: 1/4;
      grid-row:5;
    }

    a {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      margin: 0 4px;
      width: 38px;
      height: 38px;
      background-color: #F5F5F5;
      border-radius: 8px;
      @include hover-effect;
    }
  }

  &__nav-block,
  &__contacts {
    grid-row: 1/5;
    font-size: 18px;
    font-weight: 400;
    line-height: 27px;
    margin-left: auto;
    @media (max-width: 1240px) {
      margin-left: 0;
    }
    @media (max-width: 1240px) {
      grid-row: 2;
    }
    @media (max-width: $mobileMedium){
      grid-column: 1/4;
      grid-row: unset;
    }

    a {
      opacity: .7;
      @include hover-effect;
    }

    ul {
      display: flex;
      flex-direction: column;
    }
  }

  &__nav-block {
    @media (max-width: $tabletS) {
      padding-right: 30px;
    }
  }


  &__nav-item {
    display: inline-block;
    padding: 2px 0;
    margin-bottom: 4px;
  }

  &__nav-title {
    a {
      display: inline-block;
      font-size: 22px;
      font-weight: 500;
      line-height: 26.4px;
      margin-bottom: 12px;
      opacity: 1;
    }
  }

  &__contacts {
    max-width: 300px;
    margin-right: 0;
    @media (max-width: $tabletS) {
      max-width: 204px;
      margin-left: auto;
    }
    @media (max-width: $mobileMedium){
      margin-left: 0;
    }


    p {
      opacity: .7;
      margin-bottom: 12px;
    }
  }

  &__copyright {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 12px 0;
    font-size: 16px;
    line-height: 24px;
    opacity: .7;
  }
}