h1, .h1 {
  font-size: 40px;
  font-weight: 500;
  line-height: 56.88px;
  letter-spacing: 1px;
  @media (max-width: $tablet) {
    font-size: 32px;
    line-height: 46.92px;
  }
  @media (max-width: $mobile) {
    font-size: 28px;
    line-height: 40px;
  }
}

.main-title{
  font-weight: 700;
  font-size: 60px;
  line-height: 72px;
  @media (max-width: $tablet){
    font-size: 36px;
    line-height: 43.2px;
  }
  @media (max-width: $mobile){
    font-size: 28px;
    line-height: 33.6px;
  }
}


h2, .h2 {
  font-size: 32px;
  line-height: 38px;
  font-weight: 500;
  margin-bottom: 32px;
  @media (max-width: $tabletS) {
    font-size: 28px;
    line-height: 33px;
    margin-bottom: 20px;
  }
  @media (max-width: $mobile) {
    font-size: 24px;
    line-height: 28px;
    margin-bottom: 20px;
  }
}

.t-bold {
  font-weight: 700;
}


.alter-title {
  font-size: 40px;
  line-height: 57px;
  font-weight: 500;
  @media (max-width: $tablet) {
    font-size: 32px;
    line-height: 46px;
  }
  @media (max-width: $mobile) {
    font-size: 24px;
    line-height: 34px;
  }
}

.section-title{
  font-weight: 500;
  font-size: 32px;
  line-height: 46.24px;
  letter-spacing: 0.18px;
  margin-bottom: 0;
  margin-right: 10px;
  @media (max-width: $tablet){
    font-size: 28px;
    line-height: 40.46px;
  }
  @media (max-width: $mobile){
    font-size: 24px;
    line-height: 34.68px;
  }
}

h3 {
  font-weight: 500;
  font-size: 32px;
  line-height: 40px;
  margin-bottom: 20px;
  @media (max-width: $tablet){
    font-weight: 500;
    font-size: 28px;
    line-height: 35px;
  }
  @media (max-width: $mobile){
    font-weight: 500;
    font-size: 24px;
    line-height: 28.8px;
  }
}